import Vue from 'vue'
import Router from 'vue-router'
import User from '../components/User'
import SuperUser from '../components/Empty'
import DocModify from '../components/DocModify'
import DocView from '../components/DocView'
import Super from '../components/Super'
import Empty from '../components/Empty'
import DocAdd from '../components/DocAdd'
import DocDistribute from '../components/DocDistribute'

import App from '../App2'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Empty',
      component: Empty,
      props: true
    },
    {
      path: '/SuperUser',
      name: 'SuperUser',
      component: Super,
      props: true,
      children: [{
        path: '/SuperUser/DocView/:no',
        name: 'DocView',
        component: DocView,
        props: true
      }]
    },
    {
      path: '/User',
      name: 'User',
      component: User,
      props: true,
      children: [{
        path: '/User/DocView/:no',
        name: 'UserDocView',
        component: DocView,
        props: true
      }]
    },
    {
      path: '/SuperUser/DocModify/:no',
      name: 'DocModify',
      component: DocModify,
      props: true
    },
    {
      path: '/Superuser/DocAdd',
      name: 'DocAdd',
      component: DocAdd,
      props: true
    },
    {
      path: '/SuperUser/DocDistribute/:no',
      name: 'DocDistribute',
      component: DocDistribute,
      props: true
    }
  ]
})



